<template>
  <div>
    <mrc_ess_iconGroup :is_disabled="disabled"
                       @handleReturn='handleReturn'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>
    <data-info @success='success'
               :paramsProps='params'
               :disabled='disabled'></data-info>
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import { getProcedureInfo, addUpdateProcedure, delProcedure } from '@/service/api/dataStoredProcedure'
import dataInfo from '../modules/index'
export default {
  components: { dataInfo },
  props: {},
  data () {
    return {
      params: {},
      disabled: false
    };
  },
  validations: {},
  computed: {

  },
  created () { },
  watch: {},
  async mounted () {
    if (this.$store.state.iconstatus) {
      this.disabled = true
    } else {
      this.disabled = false
    }
    await this.getInfo()
  },

  methods: {
    async getInfo () {
      const res = await getProcedureInfo({
        uuid: this.$store.state.uuid
      })
      if (res.code == 200) {
        this.params = res.data
      }
    },
    async success (params) {
      const res = await addUpdateProcedure(params)
      if (res.code == 200) {
        this.$store.commit('newsTips', this.$t('message_The_modification_was_successful'))
        this.$router.go(-1)
      }
    },
    handleReturn () {
      this.$router.go(-1)
    },
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 删除
    handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
        .then(async () => {
          const del = await delProcedure({
            uuid: this.$store.state.uuid
          })
          if (del.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.go(-1)
          }
        }).catch(() => { })
    }
  },
};
</script>

